.Background,
.WishBackground {
  background-size: cover;
  height: 100vh;
  max-width: 100vw;
  margin: 0px;
}
.Container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.ImageContainer {
  position: relative;
  height: 100vh;
  width: fit-content;

  display: flex;
  justify-content: center;
  align-items: center;
}
.TitleText {
  position: absolute;
  top: 30%;
  color: rgb(243, 71, 24);
  font-family: "Bebas Neue";
  font-weight: 700;
  font-size: 54px;
}
.BirthdayText {
  position: absolute;
  top: 43%;
  font-family: "Dancing Sript", cursive;
  color: blueviolet;
  font-weight: 700;
  font-size: 40px;
}
.NameText {
  position: absolute;
  color: rgb(0, 90, 139);
  top: 25%;
  font-family: "Grey Qo", cursive;
  font-weight: bolder;
  font-size: 100px;
}
.HappyText {
  font-family: "Dancing Sript", cursive;
  font-size: 40px;
  color: white;
  text-shadow: 2px 2px 0 black, -2px -2px 0 black, 2px -2px 0 black,
    -1.5px 1.5px 0 black, 1px 0 0 black, -1px 0 0 black, 0 1px 0 black,
    0 -1px 0 black;
}
.AgeText {
  font-weight: bolder;
  font-family: "Grey Qo", cursive;
  font-size: 300px;
  margin: 90px 0px 120px;
  line-height: 0;
  color: white;
  text-shadow: 1px 1px 0 black, -1px -1px 0 black, 1px -1px 0 black,
    -12px 12px 0 black, 12px 0 0 black, -12px 0 0 black, 0 12px 0 black,
    0 -12px 0 black;
}
.IconContainer {
  position: absolute;
  background-color: rgb(96, 210, 120);
  border-radius: 50%;
  padding: 14px;
  top: 75%;
  cursor: pointer;
}
.PopperIcon {
  font-size: 44px;
  color: white;
}
.WishesContainer {
  position: absolute;
  padding: 10%;
}
.WishesText {
  color: rgb(255, 255, 255);
  background-color: rgb(32, 32, 32);
  font-size: 24px;
  font-weight: 600;
  text-align: justify;
  font-family: "Rajdhani";
  border-radius: 4px;
  line-height: 1.5;
  padding: 1px 2px;
}